const fetchData = async (setNotesList, apiPath) => {
	try {
		const response = await fetch(`/api/?type=${apiPath}`);
		const data = await response.json();
		if (data) {
			setNotesList(data);
		} else {
			setNotesList([]);
		}
	} catch (error) {
		console.error(error);
	}
};

export default fetchData;
