import React, { useState, useEffect } from "react";
import { AiOutlineSearch } from "react-icons/ai";
import "./scss/search.css";
import { useGlobalContext } from "../../context/context";
import escapeWithSpecialChar from "../../services/searchConfig";

const Search = () => {
	const { notesList, setFilteredList } = useGlobalContext();
	const [searchTerm, setSearchTerm] = useState("");

	const searchHandler = () => {
		const searchList = notesList.filter(
			(newNote) =>
				newNote.title.toLowerCase().match(escapeWithSpecialChar(searchTerm)) ||
				newNote.detail.toLowerCase().match(escapeWithSpecialChar(searchTerm))
		);

		searchList.length > 0 && setFilteredList(searchList);

		return;
	};

	useEffect(() => {
		searchHandler();
	}, [searchTerm]);

	return (
		<form>
			<div className="icon-container">
				<AiOutlineSearch className="search-icon" />
			</div>
			<input
				type="text"
				value={searchTerm}
				placeholder="Search"
				onChange={(e) => setSearchTerm(e.target.value)}
			/>
		</form>
	);
};

export default Search;
